import $ from 'jquery';

$('form').submit(function(e) {
    e.preventDefault();

    // if(!$(this).valid) {
    //     return;
    // }

    $.ajax({
        type: 'POST',
        url: '../../mailer/smart.php',
        data: $(this).serialize()
    }).done(function() {
        $(this).find('input').val('');

        $('form').trigger('reset');
    });
    alert('Форма отправлена! Ожидайте ответа от специалиста');
    return false;
});